html {
    font-size: 62.5%; }

body {
    font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
    padding: 0;
    margin: 0; }

.wrapper {
    min-height: 100%;
    overflow: hidden;

    display: flex;
    flex-direction: column; }

.header {

    &__container {
        // border: 1rem solid black
        flex: 1 1 auto;
        background-color: #445162;
        max-height: 32.5rem;
        width: 100%;
        margin: 0px auto;
        padding: 5rem;
        margin-bottom: 4rem; }

    &__flex {
        display: flex;
        justify-content: space-between; }

    &__body {
        color: #fff;
        padding: 0rem 2rem 0rem 0rem; }

    &__text {
        flex-wrap: nowrap;
        font-size: 6rem;
        padding-bottom: 3.8rem; }

    &__list li {
        font-size: 2rem;
        background: url(../icon/icon.png)  no-repeat;
        padding: 0rem 0rem 1.5rem 3.6rem;
        list-style-type: none;
        align-self: flex-start; } }

.img {
    height: 21.4rem;
    width: 46.9rem; }

.section {
    &__container {
        // border: 1rem solid black
        padding: 0rem 5rem 4.5rem 5rem;
        margin-bottom: 4.5rem; }

    &__flex {
        display: flex; }

    &__img {}



    &__body {
        color: #445162; }

    &__title {
        font-size: 3rem;
        padding-bottom: 3.5rem; }

    &__text {
        font-size: 1.6rem;
        line-height: 2.4rem;

        &-1 {
            margin-bottom: 3rem; } } }
.img2 {
    width: 43rem;
    height: 20rem;
    margin-right: 4rem; }



.columns {

    &__container {
        border: 1rem solid black;
        padding: 0rem 5rem 5rem 5rem; }

    &__flex {
        display: flex;
        flex-direction: row;
        justify-content: center; }

    &__title {
        color: #445162;
        font-size: 3rem; }

    &__square {

        &-1 {
            width: 20rem;
            height: 15rem;
            background: #445162; } } }












